nav {
  background-color: var(--blue);
}

body {
  flex-direction: column;
  min-width: 100vw;
  height: 100vh;
  display: flex;
}

main {
  background-color: var(--blue);
  padding-left: var(--x-padding);
  padding-right: var(--x-padding);
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  justify-content: space-between;
  width: 100%;
  display: flex;
  overflow-y: auto;
}

#form-content-box {
  place-self: center;
  width: 650px;
  margin-bottom: 1vh;
}

@media screen and (width <= 700px) and (orientation: portrait) {
  #form-content-box {
    place-self: unset;
    width: unset;
    max-width: 650px;
  }
}

header h1 {
  margin-top: 1em;
  font-size: max(2rem, min(7vw, 3rem));
}

header p {
  margin-top: 32px;
  font-size: max(1.2rem, min(5vw, 2rem));
  font-weight: 600;
}

p {
  margin-top: 24px;
  font-size: max(1.2rem, min(3vw, 1.2rem));
}

.footer p {
  font-size: max(1.2rem, min(5vw, 2rem));
  font-weight: 600;
}

.stripes {
  width: 100vw;
  height: 72px;
  margin-top: 24px;
  margin-bottom: 24px;
  margin-left: calc(0px - var(--x-padding));
  background: url("stripes.c032f86a.svg");
}
